import {FaRedo} from 'react-icons/fa'
import WeatherItem from './WeatherItem'

const Weather = ({weather, onRefresh}) => {
    return (
        <div class="weather-panel col-6">
            <div className='row'>
                <p className='col-10 panel-title'>WEATHER</p>
                <FaRedo className='col-2 refresh-btn' onClick={() => onRefresh()}/>
            </div>
            {
                weather.map((w) => (
                <WeatherItem weather={w} />
            ))}
        </div>
    )
}

export default Weather
