const NewsItem = ({article}) => {
    return (
        <a href={article.link} target="_blank" style={{textDecoration:"none"}}>
            <div className="news-item">
                <p>{article.title}</p>
                <p className='news-meta'>Posted: {article.published_date}</p>
                {/* <p className='news-meta'>{article.clean_url} {article.author !== null && "- " + article.author}</p> */}
            </div>
        </a>
    )
}

export default NewsItem
