import NewsItem from "./NewsItem"
import {FaRedo} from 'react-icons/fa'

const News = ({news, onRefresh}) => {
    return (
        <div className="news-panel col-6">
            <div className='row'>
                <h2 className='col-10 panel-title'>TRENDING NEWS</h2>
                <FaRedo className='col-2 refresh-btn' onClick={onRefresh}/>
            </div>
            {
                news.map((item) => (
                    <NewsItem article={item} />
                ))
            }
        </div>
    )
}

export default News
