const WeatherItem = ({weather}) => {
    const imgURL = 'https://www.weatherbit.io/static/img/icons/' + weather.weather.icon + '.png'

    return (
        <div className='weather-item'>
            <img src={imgURL} alt='weather-icon' width='100%'/>
            <p>{weather.weather.description}</p>
            <p>{weather.timestamp_local.split("T")[0]}</p>
            <p>Temp: {weather.temp} &#8451;</p>
        </div>
    )
}

export default WeatherItem
